<template>
	<div class="loginrespage">
    <com-header/>
    <div class="login-wrap">
      <div class="loginres-box">
        <h2 class="loginres-title"></h2>
        <div class="loginres-body">
          <p class="inputext addphoneicon"><input type="text" name="username" placeholder="" v-model="username"></p>
          <!--<div>
                      <p class="inputext getphonecode"><input type="password" name="userpwd" placeholder=""></p>
                      <a href="#" class="sendcode"></a>
                  </div>-->
          <p class="inputext addpwdicon"><input type="password" name="userpwd" placeholder="" v-model="userpwd"></p>
          <p class="inputext addpwdicon"><input type="password" name="userpwd" placeholder="" v-model="userpwdagain"></p>
          <!--<p class="inputext emailicon"><input type="password" name="userpwd" placeholder=""></p>-->
          <div class="rememberline fs12">
            <label class="rememberlogin"><input type="checkbox" name="savelogin" checked="true">&nbsp;&nbsp;<a href="#"></a></label>
            <span class="movetoright">
						<router-link to="/login">&gt;&gt;</router-link>
					</span>
          </div>
          <div><button class="btnstyle" @click="regUser"></button></div>
        </div>
      </div>
    </div>
    <com-foot/>
	</div>
</template>

<script>
  import comHeader from 'src/components/header'
  import comFoot from 'src/components/foot'
  export default {
    components: {
      comHeader, comFoot
    },
    data () {
      return {
        username: '',
        userpwd: '',
        userpwdagain: ''
      }
    },
    methods: {
      regUser () {
        if (this.username.trim() === '') {
          alert('')
        } else if (this.userpwd.trim() === '' || this.userpwdagain.trim() === '') {
          alert('')
        } else if (this.userpwd.trim() !== this.userpwdagain.trim()) {
          alert('')
        } else {
          let username = this.username
          let userpwd = this.userpwd
          this.$http.post('/api/user/reg', {
            username: username,
            userpwd: userpwd
          }, {}).then((response) => {
            if (JSON.parse(response.bodyText).status === 0) {
              alert('')
            } else if (JSON.parse(response.bodyText).status === 1) {
              alert('')
              this.$router.push('/login')
            }
          })
        }
      }
    }
  }

</script>

<style scoped src="assets/css/loginres.css"></style>



